.store_card{
    margin-left: auto;
    height: 400px ;    
}
.navlink{
  text-decoration: none;
  color: black;
}

.clickText:hover{
  cursor: pointer;
}

@media (min-width:420px) {
  .basketLink{
    position: fixed;
  }
}
@media (max-width:420px) {
  .basketLink{
    position: absolute;
  }
  .responsive_container_market_place{
    margin-top: 200px !important;
  }
  .basketLink{
    position: fixed;
    z-index: 100;

  }
}
.basketLink{
  display: flex;
  flex-direction: column;
  justify-content: center; 
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.likedLink{
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  
}
.basketIcon{
  height: 50px;
}

.marketCard{
  width: 18rem ;
  overflow: hidden;
  margin-bottom: 30px !important;
  margin: auto ;
  text-align: center;  
  transition: all .1s linear;
  border-radius: 15px;
  border-top: 3px solid forestgreen;
  border-bottom: 3px solid forestgreen;
}

.marketCard:hover{
  /* width: 310px;
  height: 410px; */
  padding: 0px !important;
  
}

@media (max-width:767px) {
  .store_card{
    margin-left: auto;
    height: max-content ;    
}
}

.card_description{
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;
overflow: hidden;
text-overflow: ellipsis;
margin-top: -10px;
}

.card_title{
display: -webkit-box;
-webkit-line-clamp: 2;
-webkit-box-orient: vertical;
overflow: hidden;
text-overflow: ellipsis;
}

.card-img-top{
  height: 180px;
}

.CardLinks{
  text-decoration: none;
  color: black;
  background-color: rgb(224, 248, 118);
  border-radius:0 0 33px 33px;
  padding: 2px;
  margin-bottom:5px;
}  
.sorting_selection{
  margin-top: 30px;
}
.like_button{
  background-color: white;
  border-color: yellow;
}
.basket_button{
  margin-left: 10px;
}