@import url(//fonts.googleapis.com/css?family=Montserrat:300,400,500);
.contact4 {
  font-family: "Montserrat", sans-serif;
	color: #8d97ad;
  font-weight: 300;
}
.contact-box{
  margin-top: 100px;
}
.rower{
    --bs-gutter-x: 0px !important; 
    --bs-gutter-y: 0 !important;
}

.contact4 h1, .contact4 h2, .contact4 h3, .contact4 h4, .contact4 h5, .contact4 h6 {
  color: #3e4555;
}

.contact4 .font-weight-medium {
	font-weight: 500;
}

.contact4 .form-control {
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.5);
}

.contact4 .form-control:focus {
  border-color: #ffffff;
}

.contact4 input::-webkit-input-placeholder,
.contact4 textarea::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.contact4 input:-ms-input-placeholder,
.contact4 textarea:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.contact4 input::placeholder,
.contact4 textarea::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.contact4 .right-image {
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
}
.contact-box{
  height: 100vh;
}

.input_box{
  margin-top: -70px;
}

.form-control{
  transition: all 1s linear;
  color: rgb(0, 0, 0) !important;
}

.form-control:focus{
  background-color: rgba(240, 248, 255, 0.49) !important;
  
}


textarea{  
  resize: none;
  height: 200px;
}

iframe{
  margin-top: 10%;
  border:0;
  position: relative;
  margin-left:-5% ;
}

.contact4.bg-info {
  background-image: url(../../../assets/images/earth-158806_1280.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.contact4 .text-inverse {
    color: #3e4555 !important;
}

@media (min-width: 800px) {
	.contact4 .contact-box {
    padding: 80px 105px 80px 0px;
	}
}

@media (max-width: 767px) {
	.contact4 .contact-box {
    padding-left: 15px;
    padding-right: 15px;
	}
}

@media (max-width: 1023px) {
	.contact4 .right-image {
			position: relative;
			bottom: -95px;
	}
}

