.footer-dark {
    padding:50px 0;
    color:#f0f9ff;
    background-color:#282d32;
    width: 100%;
    height: max-content;
  }
  
  .footer-dark h3 {
    margin-top:0;
    margin-bottom:12px;
    font-weight:bold;
    font-size:16px;
  }

  .icon_container{
    /* // background-image: url('../../assets/images/social_network_background.jpg'); */
    background-repeat: no-repeat;
    background-size: 100% 100%;
    padding-top: 12px;
    margin-left: auto;
    height: 50px;
    width: 300px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
   
}
.icon{
    align-self: center;
    height: 50px;
    width: 50px; 
}
.social{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
/* .gmail{
  height: 30px;
  width: 30px;
} */
  .row{
    padding-top: 50px !important;
    margin-bottom: -20px;
  }
  .footer-dark ul {
    padding:0;
    list-style:none;
    line-height:1.6;
    font-size:14px;
    margin-bottom:0;
  }
  
  .footer-dark ul a {
    color:inherit;
    text-decoration:none;
    opacity:0.6;
  }
  
  .footer-dark ul a:hover {
    opacity:0.8;
  }
  
  @media (max-width:767px) {
    .footer-dark .item:not(.social) {
      text-align:center;
      padding-bottom:20px;
    }
  }
  
  .footer-dark .item.text {
    margin-bottom:36px;
  }
  
  @media (max-width:767px) {
    .footer-dark .item.text {
      margin-bottom:0;
    }
  }
  
  .footer-dark .item.text p {
    opacity:0.6;
    margin-bottom:0;
  }
  
  .footer-dark .item.social {
    text-align:center;
  }
  
  @media (max-width:991px) {
    .footer-dark .item.social {
      text-align:center;
      margin-top:20px;
    }
  }
  
  .footer-dark .item.social > a {
    font-size:20px;
    width:36px;
    height:36px;
    line-height:36px;
    display:inline-block;
    text-align:center;
    border-radius:50%;
    box-shadow:0 0 0 1px rgba(255,255,255,0.4);
    margin:0 8px;
    color:#fff;
    opacity:0.75;
  }
  
  .footer-dark .item.social > a:hover {
    opacity:0.9;
  }
  
  .footer-dark .copyright {
    text-align:center;
    padding-top:24px;
    opacity:0.3;
    font-size:13px;
    margin-bottom:0;
  }