@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap');

:root{
	--color-white: #fff;
	--color-btn-boder: #F5F5F5;
	--color-btn-hover-bg: #6BC1FF;
	--color-btn-bg: #F5F5F5;
	--color-title-black: #333;
	--color-main: #5969F6;
}

.image{
    height: 480px;
    background-repeat: no-repeat;
    background-size: 100% 100%;  
    width: 100%;
    background-image: url("../../assets/images/saveNature.png");
    position: relative;
   
        
       
}
.image_text{
    opacity: 0;
    transition: all 2s linear;
    font-family: 'Orbitron', sans-serif;

}
.shadow{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-family: 'Orbitron', sans-serif;
    width: 100%;
    height: 100%;
    background-color: transparent;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.429);
    transition: all 1s linear;    
    
}
  .shadow:hover .image_text{
        
    opacity:1;
    color: rgb(255, 255, 255);
    display: block;
    font-size: 64px;
    font-family: 'Orbitron', sans-serif;
    height: max-content;
    width: max-content; 

}
/* .image:hover{
    cursor: url("../../assets/images/tree.png"),  url("../../assets/images/tree.png"), auto;
} */
.shadow:hover{
    opacity:1;
}
.middle_simple{
    margin-top: 10px;
    margin-bottom: -10px;
}
.services_section{
    background-color: rgb(255, 255, 255);
}
.center{
	width: 1199px;
}

.center{
	position: relative;
	float: none;
	margin: 0 auto;
}
@media (max-width: 1220px){
	.center{
		width: 98%;
	}
    .service_list li:nth-child(even){
        align-items: flex-start;
        margin-top: 0px !important;
    }
    .service_list{
        margin-top: 20px !important;
    }

}
.full_w{
	position: relative;
	float: left;
	width: 100%;
}


.services_section_in > h2{
	font-size: 55px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: var(--color-title-black);
}

.service_list{
    padding-left: 0px;
}

.services_section_in p{
	margin-top: 8px;
	font-size: 20px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: rgba(51, 51, 51, .71);
	max-width: 523px;
}

.services_section_in > p{
	margin-top: 40px;
}

.service_list{
  margin-top: -72px;
}

.service_list li{
	width: 100%;
	display: flex;
	flex-direction: column;
}

.service_list li:nth-child(odd){
	align-items: flex-end;
}

.service_list li:nth-child(even){
    align-items: flex-start;
	margin-top: -300px;
}

.service_list li img{
	width: 400px;
}

@media (max-width: 403px){
	.service_list li img{
        width: 200px; 
    }
    .service_list img:hover{
        width: 220px !important;
    }

}

.service_list li h3{
	margin-top: 60px;
	font-size: 30px;
  font-family: 'SegoeUI-Semibold';
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  color: #333;
}

.service_list li p{
	margin-top: 35px;
}

.article_photo{
    border-radius: 15px;
    
    transition: all 2s ;
}


.service_list img:hover{
    width: 470px;
}