@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300&display=swap');
.firstChangedStyle{
    height:200px;
    
    background-image: url("https://img.freepik.com/free-vector/mars-landscape-alien-planet-background-red-desert_107791-13075.jpg?auto=format&h=200");
    background-repeat: no-repeat;
    background-size: cover;
}
.secondChangedStyle{
    width: 150px;
}
/* .card{
    padding-bottom:30px ;
