.redborder{
    width: max-content;
    height: max-content; 
}
*{
    font-family: 'Merriweather', serif;

}
.aboutProduct{
    text-align: justify;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 5px;
    font-family: 'Merriweather', serif;
    font-weight: bold;
}
.productContainer{
    display: flex !important;
    padding: 10px 20px;
    border-radius: 50px;
    flex-direction: row !important;
    border: 2px solid rgba(19, 19, 19, 0.234);
}
@media (max-width: 780px){
    .productContainer{
        flex-direction: column !important;
    }
    .productImage{
        width: 180px !important;
    }
}
.productContainer:not(:first-child){    
    margin-top: 30px;
}
.productImage{
    width: 192px;
    height: 192px;
    margin-left: 0px;
}
.accountImage{
    width: 150px;
    z-index: 1
}
.accountButton{
    z-index: 1;
}
.marginTop130px{
    margin-top: 130px;
}
.f8f9fa{
    background-color: #f8f9fa;
}
.profile_name{
    transition: all .5s linear;
    padding: 5px;
    border-radius: 10px;
}
.profile_name:hover{
    background-color: rgba(48, 140, 40, 0.319);
}
.showMoreButton{
    width: 100px;
    margin: auto;
    margin-top: 10px;
    border-radius: 5px;
    background-color: #116f24b2;
    color: #ffffffb2;
    transition: all 1s;

}
.showMoreButton:hover{
    color: #ffffff;
    background-color: #116f24;
}
#toOldButton{
    display: none;
}