
*{
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.main_navbar_container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 80%;
}
.navbar_children{
    margin-top: -15px;
}
.navbar_container{
    background-color: rgb(23, 175, 12)!important;
    margin-top: 0px;
    border-radius: 0px 0px 0px 0px !important;
    height: max-content !important;
}
.navbar_children{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.626);
    transition: all 1s linear ;
}
.navbar_children:hover{
    font-size: 18px;
    color: aliceblue;;
}
.middle_simple{
    margin-bottom: 0px !important;
}

@media screen and (min-width: 1240px) { 
    .navbar_container_child{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-content: space-around;
        width: 700px;
    }
  }
  @media screen and (min-width: 992px) { 
    .navbar_container_child{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-content: space-around;
        width: 480px !important;
    }
    /* .icon_container{
        margin-right: -100px;
    } */
  }
  @media screen and (max-width: 992px) { 
    .navbar_children{
        margin-top: 10px;
    }    
    .navbar_container_child{
        width: 105px;
        height: max-content;
    }
    .middle_simple{
        width: 105px !important;
        height: max-content;
    }
    /* .icon_container{
        margin-top: -50px;
    } */
  }

  @media  (max-width: 380px) {
    .signs{
        width: max-content !important;
        font-size: 10px;
        background-color: red !important;
    }
   
  }
/* .logOut{
    padding: 5px !important;
} */
@media (max-width: 1200px){
    .signs{
        color: aliceblue;
        border-radius: 15px;
        width: 100px;
        height: 30px;
        font-size: 12px !important;
      }
}
@media (max-width: 599px){
    .icon_container{
        margin-bottom: 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-content: space-between;
        width: max-content !important;
    } 
}
@media (max-width: 310px){
    .icon_container{
        margin-bottom: 40px !important;
        margin-left: 0px !important;
        display: flex;
        flex-direction: column !important;
        justify-content: space-between;
        align-content: space-between;
        width: max-content !important;
        margin-top: 20px;
    } 
}
@media (max-width: 320px){
    .userNameArea{
        font-size: 12px;
        width: 70px;
      }
}

  .signs{
    color: aliceblue;
    border: 5px solid rgb(32, 139, 5);
    border-radius: 15px;
    font-size: 14px;
    padding: 0px 5px;
    background-color: rgb(23, 175, 12)!important;
  }
  .userNameArea{
    font-size: 12px;
  }

